import React from 'react';
import './App.css';
import './assets/css/styles.css';
import {BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import home from './pages/home';
function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route exact path="/" component={home}></Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
